import React from "react";
import "../stylesheets/layout/_fill.scss";

function Fill() {
  return (
    <form action="/signup" class="form" method="POST" name="fill-form">
      <fieldset class="fieldset data">
        <label for="name" class="form__label">
          Nombre completo <span class="form--required">*</span>
        </label>
        <input
          id="name"
          name="name"
          type="text"
          class="form__input"
          placeholder="Ej: Mary Shelley"
          required
        />

        <label for="job" class="form__label">
          Puesto <span class="form--required">*</span>
        </label>
        <input
          id="job"
          name="job"
          type="text"
          class="form__input"
          placeholder="Ej: Master of creatures"
          required
        />
      </fieldset>

      <fieldset class="fieldset">
        <label for="photo" class="form__label">
          Imagen de perfil <span class="form--required">*</span>
        </label>
        <div class="form__label-div">
          <button class="form__label--bt">Añadir imagen</button>
          <input id="photo" name="photo" type="file" class="hidden" />
          <div class="form__img--container"></div>
        </div>
      </fieldset>

      <fieldset class="fieldset social">
        <label for="email" class="form__label">
          Email <span class="form--required">*</span>
        </label>
        <input
          id="email"
          name="email"
          type="email"
          class="form__input"
          placeholder="Ej: mary.shelley@gmail.com"
          required
        />

        <label for="phone" class="form__label">
          Teléfono
        </label>
        <input
          id="phone"
          name="phone"
          type="tel"
          class="form__input"
          placeholder="Ej: 666 66 66 66"
        />

        <label for="linkedin" class="form__label">
          Linkedin <span class="form--required">*</span>
        </label>
        <input
          id="linkedin"
          name="linkedin"
          type="text"
          class="form__input"
          placeholder="Ej: dr-frankenstein"
          required
        />

        <label for="github" class="form__label">
          GitHub <span class="form--required">*</span>
        </label>
        <input
          id="github"
          name="github"
          type="text"
          class="form__input"
          placeholder="Ej: frankensteins-monster"
          required
        />
        <label for="required-fields" class="form__label">
          <span class="form--required">*</span> Campos obligatorios
        </label>
      </fieldset>
    </form>
  );
}

export default Fill;
